// 
import React from 'react';
import { Redirect } from 'react-router';

import { AWRouteState, AWMiddlewareFunc } from 'aw-react-router';

import { RouteMeta } from '@/routes/type';
import Token from '@/auth/token';
import Authority from '@/auth/authority';
import { isUndefined } from '@/tools/instance';


/**
 * 
 * whiteList:  name 
 */
export function loginRequiredMiddleware(whiteList?: string[]): AWMiddlewareFunc {
  return function (state: AWRouteState) {
    // 
    if (whiteList && whiteList.length) {
      if (whiteList.indexOf(state.name) !== -1) {
        return null;
      }
    }

    if (Token.isLogin()) {
      return null;
    }

    return <Redirect to="/login" />;
  }
}

/**
 * 
 * checkList:  route name 
 */
export function guestMiddleware(checkList?: string[]): AWMiddlewareFunc {
  return function (state: AWRouteState) {
    if (checkList && checkList.length) {
      if (checkList.indexOf(state.name) === -1) {
        return null;
      }
    }

    if (!Token.isLogin()) {
      return null;
    }

    return <Redirect to="/" />;
  }
}


/**  () */
export function authCheckMiddleware() {
  return function (state: AWRouteState<RouteMeta>) {
    const meta = state.meta || {};
    const auths = meta.auth || [];
    const strict = isUndefined(meta.authUnstrict) ? true : !meta.authUnstrict;

    //  ()
    if (!auths.length) {
      return null;
    }
    if (Authority.checkAll(auths, strict)) {
      return null;
    }

    return <Redirect to="/unauthorized" />
  }
}
