import { AxiosRequestConfig } from 'axios';

export const URLENCODED_CONTENT_TYPE = {
  'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
};
export const JSON_CONTENT_TYPE = {
  'Content-Type': 'application/json;charset=UTF-8',
};
export const CommonErrorMessage = '';

export interface MyAxiosRequestConfig extends AxiosRequestConfig {
  json?: boolean     //  JSON_CONTENT_TYPE
  showErrorMessage?: boolean //  message
}

/**  */
export interface APIResponseData<T = any> {
  code: number
  data: T
  msg: string
  token: string | null
}

/** services  */
export interface CommonResponse<T = any> {
  status: boolean
  message: string
  data: T
}